      <van-row align="center" gutter="20" v-if="index % 2 == 0" class="row">
        <van-col span="12" @click="onClickVedio(index)">
          <van-image
            fit="fill"
            :src="filmList[index].cover"
            :height="imageHeight"
            class="item"
          >
            <span v-if="true" class="sidai"
              ><div style="color: #fff">NEW</div></span
            >
            <span class="duration"
