          </van-image>
          <div class="van-ellipsis video-title">
            {{ filmList[index].title }}
          </div>
        </van-col>
        <van-col
          span="12"
          @click="onClickVedio(index + 1)"
          v-if="index + 1 < filmList.length"
        >
          <van-image
            fit="fill"
            :height="imageHeight"
            class="item"
            :src="filmList[index + 1].cover"
          >
            <span v-if="false" class="sidai"
              ><div style="color: #fff">NEW</div></span
            >
            <span class="duration"
              ><div style="color: #fff">
                {{ timeString(filmList[index + 1].duration) }}
              </div></span
            >
          </van-image>
          <div class="van-ellipsis video-title">
