<script>
import config from "../config";
import {
  NavBar,
  Image,
  Col,
  Row,
  List,
  Loading,
  Field,
  Button,
  Icon,
} from "vant";
import Fuse from "fuse.js";
// import SearchData from "./search";
// import page0 from "./page-000.json";
// import page1 from "./page-001.json";
// import page2 from "./page-002.json";
// import page3 from "./page-003.json";
// import page4 from "./page-004.json";
// import page5 from "./page-005.json";
// import page6 from "./page-006.json";
// import page7 from "./page-007.json";
// import page8 from "./page-008.json";
// import page9 from "./page-009.json";

export default {
  components: {
    [Icon.name]: Icon,
    [NavBar.name]: NavBar,
    [Image.name]: Image,
    [List.name]: List,
    [Col.name]: Col,
    [Row.name]: Row,
    [Loading.name]: Loading,
    [Button.name]: Button,
    [Field.name]: Field,
  },

  mounted() {
    this.$http.get("./search.json").then((response) => {
      // this.info = response;
      console.log(response);
      this.SearchData = response.data;
    });

    this.imageHeight = ((window.screen.width / 2 - 10) / 9) * 16 * 0.9;
  },

  data() {
    return {
      inputField: false,
      active: "电影",
      currentTag: "电影",
      currentPage: 1,
      list: [],
      filmList: [],
      loading: false,
      finished: false,
      imageHeight: 0,
      inputValue: "",
      goodsClass: "goods",
      pageIndex: 0,
      filmLoadIndex: 0,
      pages: ["./page-000.json"],
    };
  },

  computed: {},

  methods: {
    onClickRight() {
      this.inputField = !this.inputField;
      if (this.inputField) {
        this.goodsClass = "search-goods";
      } else {
        this.goodsClass = "goods";
      }
    },

    onClickVedio(index) {
      window.films[this.filmList[index].slug] = this.filmList[index];
      this.$router.push({
        path: "/sub/",
        query: { id: this.filmList[index].slug },
      });
    },

    timeString(value) {
      let hour = Math.floor(value / 3600);
      if (hour < 10) {
        hour = "0" + hour;
      } else {
        hour = "" + hour;
      }
      let result = value % 3600;
      let minute = Math.floor(result / 60);
      if (minute < 10) {
        minute = "0" + minute;
      }
      result = result % 60;
      let second = result;
      if (second < 10) {
        second = "0" + second;
      }
      return hour + ":" + minute + ":" + second;
    },

    genPageString() {
      let temp = "" 
      if (this.pageIndex < 10) {
        temp = "00" + this.pageIndex;
      } else if (this.pageIndex < 100) {
        temp = "0" + this.pageIndex;
      } else if (this.pageIndex < 1000) {
        temp = "" + this.pageIndex;
      } else { 
        temp = "" + this.pageIndex;
      }
      return "./page-"  + temp + ".json";
    },

    getPageData(newPage = false) {
      this.$http
        .get(this.genPageString())
        .then((response) => {
          // this.info = response;
          console.log(response);
          for (let i = 0; i < response.data.length; i++) {
            this.list.push(response.data[i]);
          }
          this.appendFilm();
          this.pageIndex++;
        })
        .catch((err) => {
          this.loading = false;
          this.finished = true;
        });
    },

    getData() {
      // if (this.pageIndex >= this.pages.length) {
      //   return;
      // }
      // this.list = this.pages[this.pageIndex];
      // console.log(this.list);
      // for (let i = 0; i < this.list.length; i++) {
      //   this.filmList.push(this.list[i]);
      // }
      return;
      this.$http.get(config.api).then((response) => {
        // this.info = response;
        this.list = response.data;
        console.log(response);
        for (let i = 0; i < this.list.length; i++) {
          this.pageList.push(this.list[i]);
        }
      });
    },

    appendFilm() {
      // 每次加入20个影片
      console.log(this.list.length);
      for (var i = 0; i < 20; i++) {
        if (this.filmLoadIndex + i < this.list.length) {
          this.filmList.push(this.list[this.filmLoadIndex + i]);
        } else {
          break;
        }
      }
      this.filmLoadIndex += i;
      console.log(this.filmLoadIndex);
      this.loading = false;
    },

    onSearch() {
      const fuse = new Fuse(this.SearchData);
      const result = fuse.search(this.inputValue);
      this.pageIndex = 0;
      this.filmList = [];
      console.log(result);
      for (let i = 0; i < result.length; i++) {
        // if () {
        // }
      }
    },

    onLoad() {
      if (this.loading && this.finishedLoad) {
        return;
      }
      this.loading = true;
      // this.loading = true;
      // this.$http.get(config.api).then((response) => {
      // 加载状态结束

      // 数据全部加载完成
      if (this.filmLoadIndex + 20 > this.list.length) {
        this.getPageData();
      } else {
        this.appendFilm();
      }

      // this.list = response.data;
      // console.log(this.list);
      // for (let i = 0; i < this.list.length; i++) {
      //   this.pageList.push(this.list[i]);
      // }
      // });
    },
  },
};
</script>

